'use client'

import { error } from "console"
import { ChangeEvent, useEffect, useState } from "react"

const BASE_URL = "http://localhost:8080/api"

interface Todo {
  id: number
  content: string
  createdAt: string
}

const TodoList = () => {
  const mock = [
    { id: 0, content: "Hello world1", createdAt: "today" },
    { id: 1, content: "Hello world2", createdAt: "today" },
    { id: 2, content: "Hello world3", createdAt: "today" },
  ]

  const [todos, setTodos] = useState<Todo[]>(mock)
  const [updatedTodoId, setUpdatedTodoId] = useState<number>()
  const [updateTodoContent, setUpdateTodoContent] = useState<string>()
  const [originalTodo, setOriginalTodo] = useState<string>()


  const fetchTodos = async () => {
    try {
      const res = await fetch(`${BASE_URL}/todos`)
      if (!res.ok) throw new error
      
    } catch (error) {
      
    }
  }

  const handleInputChange = (e: any) => {
    setUpdateTodoContent(e.target?.value)
  }

  const handleSelectUpdateTodo = (todo: Todo) => {
    setUpdatedTodoId(todo.id)
    setUpdateTodoContent(todo.content)
    setOriginalTodo(todo.content)
  }

  const handleClearUpdate = () => {
    setUpdatedTodoId(undefined)
    setUpdateTodoContent(undefined)
    setOriginalTodo(undefined)
  }

  const handleUpdateTodo = (id: number) => {
    try {
      
    } catch (error) {
      
    }
  }

  const handleDeleteTodo = (id: number) => {
    
  }

  useEffect(()=>{

  }, [])

  return (
    <section className="max-w-3xl shadow-xl rounded-xl p-4 mx-auto">
      <h2 className="text-2xl font-bold text-center border-b-2 border-neutral-100">Todo List</h2>
      <div className="flex flex-col">
        {todos.map((todo) => (
          <div className="flex justify-between gap-3 p-3 border border-neutral-200" key={todo.id}>
            { updatedTodoId == todo.id ? (
              <>
                <input 
                  type="text" 
                  className="border border-neutral-400 p-1 w-full"
                  value={updateTodoContent}
                  onChange={handleInputChange} 
                />
                <div className="flex gap-3">
                  <button 
                    className="bg-blue-500 p-1 font-bold text-white rounded-lg cursor-pointer disabled:cursor-not-allowed" 
                    onClick={handleClearUpdate}
                  >
                    Cancel
                  </button>
                  <button 
                    className="bg-green-500 p-1 font-bold text-white rounded-lg cursor-pointer disabled:cursor-not-allowed" 
                    onClick={()=> handleUpdateTodo(todo.id)}
                  > 
                    Confirm
                  </button>
                </div>
              </>
            ) : (
              <>
                <p>{ todo.content }</p>
                <div className="flex gap-3">
                  <button 
                    className="bg-blue-500 p-1 font-bold text-white rounded-lg cursor-pointer disabled:bg-neutral-400 disabled:cursor-not-allowed" 
                    onClick={()=>handleSelectUpdateTodo(todo)}
                  >
                    Update
                  </button>
                  <button 
                    className="bg-red-500 p-1 font-bold text-white rounded-lg cursor-pointer disabled:cursor-not-allowed" 
                    onClick={()=>handleDeleteTodo(todo.id)}
                  >
                    Delete
                  </button>
                </div>
              </>
            )}
            
          </div>
        ))}
      </div>
    </section>
  )
}
export default TodoList